			晨夜会员服务协议
		</view>
		<view style="width:100%; text-align: right;font-weight: bold; margin-bottom: 20rpx;">版本生效日期：2022年7月6日</br>版本号:2022 V1.0</view>
		
		<text style="margin-top: 20rpx;">
			<text style="color: #f8f8f8;">空格</text>
			欢迎您使用晨夜会员服务！\n
			<text style="color: #f8f8f8;">空格</text>为使用晨夜会员服务，请您仔细阅读、理解并遵守《晨夜会员服务协议》（以下简称“本协议”）。\n\n
			<text style="color: #f8f8f8;">空格</text><text style="font-weight: bold;">本协议由您与厦门晨露睿萱信息科技有限公司（以下简称“晨夜车管家”）共同缔结，对双方具有法律效力。请仔细阅读以下全部内容，特别是以黑体加粗或其他合理方式提示您注意的条款，该等条款可能与您的权益存在重大利害关系，包括但不限于晨夜车管家责任、您享有的权利、争议解决方式及司法管辖条款等，请您留意重点阅读。如果您认为本协议中加粗条款可能导致您的部分或全部权利或利益受损，请您在确认同意本协议之前或在使用本服务之前务必再次阅读前述条款，并在您自愿接受该等条款的前提下使用本服务。\n
			<text style="color: #f8f8f8;">空格</text>如果您不同意本协议的任一或全部条款内容，请不要以确认的形式（包括但不限于：点击、进入购买程序等）进行下一步操作或使用本协议项下服务。当您确认接受本协议或开始使用本协议项下晨夜车管家服务时，即表示您与晨夜车管家已达成协议关系，并自愿接受并遵守本协议项下的全部约定。\n
			<text style="color: #f8f8f8;">空格</text>若您是未成年人，请您在法定监护人的陪同下阅读并确认本协议条款。\n
			\n一、服务说明\n
			1. 晨夜会员：</text>是指完成成为晨夜会员的所有程序，且在遵守本协议的前提下，在会员时长有效期内享受晨夜车管家提供的VIP会员服务的自然人，本协议项下晨夜会员又称“您”或“会员”。
			\n<text style="font-weight: bold;">2. 晨夜会员服务：</text>是由晨夜车管家向您提供的专享的会员权益，会员权益内容说明请您查看本协议第三条，具体权益内容以晨夜车管家“VIP特权”页面中描述为准。
			\n<text style="font-weight: bold;">3. 晨夜会员服务协议：</text>是《晨夜车管家用户服务协议》《晨夜车管家隐私政策》的补充协议，是其不可分割的一部分，与其具有同等法律效力，本协议如与前述协议发生冲突的，以本协议为准。
			\n<text style="font-weight: bold;">4. 晨夜会员服务规则：</text>是晨夜车管家在晨夜车管家平台已经或未来不时发布的与VIP会员服务相关的协议、VIP会员守则、活动规则、使用规则、公告、提示、通知及在线FAQ等内容，晨夜车管家会通过系统提示和信息推送和后台公告等形式发布，上述内容一经正式发布即生效，为本协议不可分割的组成部分。
			\n\n<text style="font-weight: bold;">二、 会员账号开通、管理及使用、注销</text>
			\n<text style="font-weight: bold;">\n（一）  会员账号开通\n</text>
			\n1.   您可以通过支付会员服务费或通过营销活动等晨夜车管家认可的途径成为晨夜会员，当您完成成为晨夜车管家会员的所有程序，您通过登录您的晨夜会员账号方可享受晨夜会员服务。
			\n2.   您可以通过登录晨夜会员中心免费查询您的账号信息详情，包括已开通的服务内容、服务期限。
			\n<text style="font-weight: bold;">3.   请您特别注意：</text>
			\n3.1  如您在晨夜车管家上看到晨夜会员服务以外的其他付费服务<text style="font-weight: bold;">（包括但不限于上门洗车费用，上门养车费用，代驾费用等）</text>，并且想了解相关服务，请您仔细阅读相关服务页面的规则或服务协议，并请关注服务提供方；如您需要使用或开通该类服务，将由对应服务提供方向您提供服务并收取费用，后续对于该服务有任何问题，请您联系服务提供方。
			<text style="font-weight: bold;">\n3.2 如您选择订购联合会员服务或因购买晨夜会员服务而获赠第三方会员服务的，晨夜车管家仅提供晨夜会员服务，</text>不对第三方的会员服务负责。第三方会员服务的权益、使用、收费规则等问题由第三方执行和解释。
			<text style="font-weight: bold;">\n（二）  账号管理及使用\n
			1. 账号管理
			1.1 您应正确使用及妥善保管、维护您的晨夜会员账号及密码，如发生泄漏、遗失、被盗等行为，而该等行为并非晨夜车管家法定过错导致，损失将由您自行承担。</text>
			\n1.2 如果您发现有人未经授权使用了您的账号或您的账号存在其他异常情况导致无法正常登录使用的，则您需要按照晨夜车管家官方公布的账号找回流程进行账号找回。在找回过程中，晨夜车管家可能会要求您提供相应信息及/或证明资料，<text style="font-weight: bold;">请确保您所提供的内容真实有效，否则将可能无法通过晨夜车管家的验证而导致找回失败。
			1.3 为保护您的账号安全 ，防止账号被盗等情况发生，晨夜车管家可定期或不定期采用不同的方式对您账号的使用安全进行检查、验证，包括但不限于主动联系您进行身份验证、短消息验证、邮箱认证等。<text style="font-weight: bold;">如您无法完成验证或无正当理由拒绝验证的，晨夜车管家可合理怀疑您的账号出现异常或被盗，因而中止向该账号提供服务或采取进一步措施。</text>
			\n\n2. 账号使用</text>
			\n2.1 合法获取及使用
			(1) 晨夜会员服务，仅限于您在晨夜车管家内使用，任何以破解、转译、转录等非法手段将晨夜车管家服务与晨夜车管家平台分离的行为，均不属于本协议中约定的晨夜会员服务，晨夜车管家保留追究侵权行为人法律责任及索赔的权利。
			(2) 任何以盗窃、利用系统漏洞（包括但不限于机器人软件、蜘蛛软件、爬虫软件、刷屏软件等）、通过任何非晨夜车管家官方或授权途径获得的晨夜会员服务（包括但不限于购买、租用、借用、分享、受让等方式获得）、恶意利用或破坏晨夜会员活动获得的会员服务，均不获本协议项下的相关保护，晨夜车管家有权取消及/或不予提供VIP会员服务，所有产生的损失及责任由侵权行为人自行承担。
			(3) 除经晨夜车管家授权代理商或者与第三方平台进行联合会员的销售外，晨夜车管家从未授权其他任何第三方销售、转让晨夜会员资格。任何未经晨夜车管家明示授权而销售、转让晨夜会员资格的行为属于非法销售、非法转让，晨夜车管家有权追究其法律责任。
			(4) 您开通、使用晨夜会员服务及/或参加相关活动时，应通过晨夜车管家官方公布/授权的方式进行。通过其他渠道、途径、方式非法获取的晨夜会员服务（包括且不限于账号、会员权益等）或参加活动获取福利（包括但不限于获得实体/虚拟礼品、会员权益等），均不获本协议项下的相关保护。<text style="font-weight: bold;">且一经发现，晨夜车管家有权立即就相关服务做出删除、取消、清零等处理， 或采取其他如中止或终止对该用户提供服务等账号管理措施，所有因此产生的损失及责任由侵权行为人自行承担。</text>
			\n(5) 您参加会员活动时，应按照活动规则、以合法且不损害活动举办方利益的方式进行。如您利用系统漏洞、规则设置缺陷、系统设置错误、滥用会员身份等参加活动并获取相关福利的，均不获本协议项下的相关保护，且晨夜车管家有权采取相关措施进行处理，所有因此产生的损失及责任由侵权行为人自行承担。
			(6) 晨夜会员服务，是在您遵守本协议及相关法律、法规、政策、公序良俗等的前提下，晨夜车管家给予您一项个人的、不可转让的、非商业用途的、可撤销的、有期限及非排他性的许可。
			(7) 您理解并同意，您不得将享有晨夜会员服务的账号以任何方式提供给他人使用。否则，因此产生的任何法律后果及责任均由您自行承担，且晨夜车管家有权中止或终止对您提供服务。
			<text style="font-weight: bold;">\n\n2.2 权利归属
			&emsp;&emsp;您理解并同意：您拥有合法获得的晨夜会员账号下晨夜会员服务、虚拟社区商品等服务和产品的使用权，上述服务和产品及其衍生物的所有权及知识产权均归晨夜车管家所有，但是经合法渠道取得的实体产品所有权或晨夜车管家另有公告说明的除外。
			\n2.3账号注册</text>
			\n（1）因微信小程序渠道允许用户不使用手机号注册登录，而晨夜会员是通过手机号绑定的，仅可在手机号绑定的微信号使用。若您的手机号被其它微信号绑定，将会导致您所使用的微信号无法绑定您手机号的晨夜会员，由此造成的您的损失，您应当自行承担。
			（2）晨夜车管家在此建议您按照页面操作指引绑定您的晨夜车管家注册账号，以便可在您的微信上使用会员权益及更好地享受晨夜车管家所提供给您的会员权益。
			\n<text style="font-weight: bold;">（三）账号注销\n</text>
			\n&emsp;&emsp;除晨夜车管家按照法律法规规定及本协议约定注销会员账号外，您可以通过在线申请注销或联系晨夜车管家的客服或通过其他晨夜车管家公示的方式注销会员账号。账号一旦注销，您将无法继续使用晨夜会员服务。且您曾通过该账号使用的晨夜车管家或/及其关联公司的产品与服务下的所有内容、信息、数据、记录将会被删除或匿名化处理；除法律法规另有规定，您也无法再检索、访问、获取、继续使用和找回，也无权要求晨夜车管家找回。 账号注销流程、注销的前提条件、注销的后果承担及注意事项等请您详见<text style="color: #007AFF;">《晨夜车管家账号注销协议》</text>
			\n<text style="font-weight: bold;">\n\n三、 晨夜会员权益及服务期限、收费\n
			（一）  权益内容\n</text>
			\n1. 晨夜会员权益，即晨夜车管家为VIP会员提供的内容、身份、福利方面的优惠及增值服务，包括但不限于服务8.8折、免代驾费用、免费车况检测、优先服务安排等，具体以晨夜特权页面的说明或者实际提供为准，本协议项下的会员权益名称与页面的说明不一致的，不影响您所实际享受的会员权益的内容。
			\n<text style="font-weight: bold;">2. 晨夜车管家可能会根据法律法规及政策变更、自身运营策略对权益内容作出部分调整，包括但不限于服务折扣、免费项目或优先特权。</text>
			\n\n3. 您通过晨夜车管家指定渠道开通本服务后，可能会由于您使用的软件版本、设备、操作系统等的不同以及其他第三方原因导致您实际可使用的具体服务有一定差别。
			\n<text style="font-weight: bold;">（二）  服务期限\n</text>
			\n1. 晨夜会员服务的服务期限以会员自行选择并支付相应会员费用的期限为准，具体期限您也可以登录晨夜车管家充值中心或者本服务的相应页面查询。该期限不因您未使用或对视频的暂停、倒回等情况而延长。当会员服务期限到期后，晨夜车管家将停止继续向您提供VIP会员服务。<text style="font-weight: bold;">自动续费的VIP会员服务在当前周期内的服务有效期到期前，您可以单方面停止自动续费，晨夜车管家亦可以根据运营策略需要而单方面停止继续向您自动续费服务，VIP会员服务期限自当前服务周期届满之日起终止。
			\n2. 请您理解，由于互联网服务的特殊性，为向您提供优质服务，晨夜车管家须不定时进行服务器的维修、调整、升级等，且在黑客攻击、计算机病毒侵入、监管机构政策通知等不可抗力事件发生后，晨夜车管家须解决故障并处理相关侵权事宜，晨夜车管家将尽可能降低该等情形对您的影响。
			\n（三）  收费\n</text>
			\n1. 本服务是晨夜车管家提供的收费服务，您须在按照本服务的收费标准支付相应费用后，方可使用本服务。此外，您也可通过晨夜车管家认可的其他方式享有、使用本服务。
			\n2. 您可通过晨夜车管家实际支持的付费方式完成VIP会员费用的支付，如银行卡支付、第三方支付等。请您注意，若您使用您的苹果账户或与您的会员账号绑定的通信账户进行支付，此付费方式为代收费运营商托收的付款方式，您通过此种付费方式付费可能存在一定的商业风险（如不法分子利用您账户或银行卡等有价卡等进行违法活动），该等风险可能会给您造成相应的经济损失，您应自行承担全部损失。
			\n<text style="font-weight: bold;">3. 晨夜车管家可能会根据本服务的整体规划、版权采购价格的变化等，对本服务收费标准(包括但不限于自动续费、额外付费等)、收费方式等进行调整（调整包含变更、取消、减少、增加相应内容），并在相关服务页面或以合理方式事先通知您。若您在购买和续费时，相关服务的价格发生了调整的，应以晨夜车管家公示的现时有效的价格为准（但晨夜车管家与您另有约定的情形除外）。
			\n4. VIP会员服务系网络商品和虚拟商品，会员费用是您所购买的会员服务所对应的网络商品价格，而非预付款或者存款、定金、储蓄卡等性质，VIP会员服务一经开通后不可退款。如因VIP会员服务存在重大瑕疵导致您完全无法使用等晨夜车管家违约情形、本协议另有约定、法律法规要求必须退款的或晨夜车管家同意退款等除外。
			晨夜车管家在此特别提醒您，您在购买VIP会员服务（包括自动续费服务）之前应仔细核对账号信息、购买的服务内容、价格、服务期限等信息。
			\n（四）其他VIP会员的权利及限制\n\n</text>
			1. 您在使用晨夜会员服务过程中，应遵守宪法法律，包括但不限于《中华人民共和国著作权法》、《中华人民共和国网络安全法》、《中华人民共和国计算机信息系统安全保护条例》、《计算机软件保护条例》、《信息网络传播权保护条例》等有关计算机及互联网规定的法律、法规；应遵守公共秩序，尊重社会公德；不得危害网络安全；不得利用网络从事危害国家安全、荣誉和利益，煽动颠覆国家政权、推翻社会主义制度，煽动分裂国家、破坏国家统一，宣扬恐怖主义、极端主义，宣扬民族仇恨、民族歧视，传播暴力、淫秽色情信息，编造、传播虚假信息扰乱经济秩序和社会秩序，以及侵害他人名誉、隐私、知识产权和其他合法权益等活动。在任何情况下，晨夜车管家一旦合理地认为用户存在上述行为的，出于维护网络安全、公共秩序等必要性，可以在任何时候，不经事先通知，终止向该用户提供服务。
			\n2. 您理解并同意，由于您使用的设备终端的产品技术不同，晨夜会员权益和福利可能在不同终端有所差异，具体请见晨夜车管家的页面说明或该终端可以支持的服务说明。晨夜车管家有权根据法律规定及政策变更、运营需求等进行VIP会员权益及特色会员服务内容的全部或部分调整，就前述权益的调整，晨夜车管家将在“VIP特权”页面、相应服务页面或以其他合理方式进行发布，并在发布后生效，我们建议请您仔细阅读。晨夜车管家将尽力确保上述调整不会损害会员已有利益，如您对调整有异议，请您联系我们的客服。
			<text style="font-weight: bold;">\n\n\n四、自动续费（连续包）服务\n\n</text>
			1. 自动续费（连续包）服务是指在会员已购买VIP会员服务的前提下，出于您对于自动续费的需求，避免您因疏忽或其他原因导致未能及时续费造成损失而推出的服务。<text style="font-weight: bold;">您选择开通自动续费服务的，即您授权晨夜车管家可在会员自动续费期限到期当日和到期后，委托支付渠道（如支付宝等）代扣下一个计费周期的费用，部分由运营商或支付渠道根据实际情况自行决定扣费周期的以实际扣费时间为准，如手机话费渠道、ios渠道等。</text>该服务实现的前提是您已开通自动续费及绑定相关账户，且可成功从其上述账户中足额扣款。
			\n<text style="font-weight: bold;">2. 计费周期：</text>月度、季度、年度等（具体以晨夜车管家页面展示为准），您可自行选择。
			\n<text style="font-weight: bold;">3. 晨夜车管家将于您所选择的晨夜车管家服务期届满前向您以发送短信或站内信等方式发送连续包服务续费提示，由您自主选择是否取消，若您选择不取消，则视为您同意晨夜车管家按照本协议进行不定期的扣款尝试，一旦扣款成功，晨夜车管家将为您开通下一个计费周期的自动扣费服务。
			\n\n五、个人信息保护\n\n</text>
			1. 晨夜车管家非常重视您个人信息的保护，在您使用晨夜车管家提供的服务时，您同意晨夜车管家按照在晨夜车管家公布的隐私权政策收集、存储、使用、披露和保护您的个人信息。晨夜车管家希望通过隐私权政策向您清楚地介绍晨夜车管家对您个人信息的处理方式，因此晨夜车管家建议您完整地阅读隐私权政策，以帮助您更好地保护您的隐私权。 更多关于个人信息处理和保护规则、用户对个人信息的控制权等内容，请查阅《晨夜车管家隐私政策》。
			\n2. 晨夜车管家非常注重未成年人的保护。若用户未满18周岁，则为未成年人，应在其监护人监护、指导下阅读本协议和使用本VIP会员服务。
			\n3. 监护人应指导子女上网应该注意的安全问题，防患于未然。若监护人同意未成年人使用晨夜车管家提供的本VIP会员服务，必须以监护人名义申请消费，并对未成年人使用本会员服务进行正确引导、监督。未成年人行使和履行本协议项下的权利和义务即视为已获得了监护人的认可。
			\n4. 晨夜车管家提醒未成年人用户在使用晨夜会员服务时，要善于网上学习，认清网络世界与现实世界的区别，避免沉迷于网络，影响日常的学习生活。
			\n<text style="font-weight: bold;">\n六、通知和送达\n</text>
			\n<text style="color: #f8f8f8;">空格</text>对于晨夜会员所有的通知均可通过晨夜车管家网页公告、站内消息、客户端推送、公众号通知、会员预留的电子邮件、手机短信或常规的信件传送等方式进行，该等通知于发送之日视为已送达用户。请您应确保预留的联系方式为正确及有效，并在变更后及时登录晨夜车管家修改，如因您未提供正确的联系信息或未及时告知新的联系方式，导致无法及时收取通知，将由您自行承担由此产生的损失及法律后果。
			\n<text style="font-weight: bold;">\n七、服务的中止/终止及变更\n</text>
			\n1.   本服务的中止或终止包含如下情况：
			1.1 您主动中止或终止，包括但不限于会员到期未进行续费等；
			1.2  因为您的违约或违规行为，晨夜车管家主动中止或终止服务的；
			1.3  因国家或相关政府监管部门要求或发生不可抗力事件时，晨夜车管家中止或终止服务的；
			1.4  其他根据法律法规应中止或终止服务的。
			\n2.   中止或终止服务后，晨夜车管家有权利但无需确保您收到特别提示或通知。当您发现无法登录或享受服务时，可以咨询晨夜车管家客服人员。
			\n3.   中止或终止后的处理：\n
			<text style="font-weight: bold;">3.1 除法律规定的责任外，晨夜车管家对本服务的中止或终止对您和第三人不承担任何责任；
			3.2 除法律规定或本协议另有约定外，已收取的费用不予退还；
			3.3 在一定合理时间内保留相关视频数据信息，其他VIP会员权益将根据网站规定进行变更或减少；
			3.4 在晨夜车管家正在进行的交易，晨夜车管家将根据情况合理判断，是否继续进行或终止相关交易，并视情况决定是否进行退费或扣除相关费用作为违约金。</text>
			\n\n4. 对于本协议或规则可能发生的变更（包括但不限于本协议修改、服务细则调整、会员权益调整等），晨夜车管家将根据本协议的约定相应通知您，请您仔细阅读并选择是否按照调整和/或变更后的本协议/服务细则/会员权益等继续选择本服务。如您同意上述调整和/或变更，您可以采用接受服务的形式进行下一步操作或继续使用本服务，以表明您已经接受上述调整和/或变更；如您不同意调整和/或变更，我们建议您立即停止使用本服务。如您对上述调整和/或变更有任何问题，可以咨询客服。
			\n<text style="font-weight: bold;">\n八、争议解决和法律适用\n</text>
			\n1. 本协议条款的生效、履行、解释及争议的解决均适用中华人民共和国法律，本协议条款因与中华人民共和国现行法律相抵触而导致部分无效，不影响其他部分的效力。
			\n2. 如就本协议内容或其执行发生任何争议，应尽量友好协商解决；协商不成时，任何一方均可向被告所在地人民法院提起诉讼。
			\n<text style="font-weight: bold;">\n九、联系我们\n</text>
			\n<text style="color: #f8f8f8;">空格</text>如您对本协议或使用VIP会员服务的过程中有任何问题（包括问题咨询、投诉等），晨夜车管家专门为您提供了多种反馈通道，希望为您提供满意的解决方案：(1)您可与晨夜车管家在线客服联系或者在线提交意见反馈；(2)您可以拨打晨夜车管家的客服电话18120718168与晨夜车管家联系。
			\n<text style="color: #f8f8f8;">空格</text>我们会在收到您的反馈后尽快向您答复。
			\n<text style="font-weight: bold;">\n十、其他\n</text>
			\n1. 本协议任一条款被视为废止、无效或不可执行，该条应视为可分的且并不影响本协议其余条款的有效性及可执行性。
			\n2. 本协议的标题仅为方便及阅读而设，并不影响正文中任何条款的含义或解释。
